<style scoped>
.search_width {
  width: 25%;
  padding-right: 40px;
  margin-bottom: 15px;
  float: left;
}
.search_width .inputs {
  width: 80%;
}
label {
  font-size: 14px;
  line-height: 32px;
}
.tablebox{
    clear: both;
    padding-top: 15px;
}
</style>
<template>
  <div>
    <!-- 搜索条件 -->
    <div>
      <p class="search_width">
        <label for>客户：</label>
        <Input v-model="value" placeholder="客户" class="inputs" />
      </p>
      <p class="search_width">
        <label for>日期：</label>
        <DatePicker type="daterange" :clearable="false" class="inputs"></DatePicker>
      </p>
      <p class="search_width">
        <label for>供应商：</label>
        <Input v-model="value" placeholder="供应商" class="inputs" />
      </p>
      <p class="search_width">
        <label for>型号：</label>
        <Input v-model="value" placeholder="型号" class="inputs" />
      </p>
      <p class="search_width">
        <label for>品牌：</label>
        <Input v-model="value" placeholder="品牌" class="inputs" />
      </p>
      <p class="search_width">
        <label for>状态：</label>
         <Select v-model="model1" class="inputs" @on-change='changestatus'>
              <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
         </Select>
      </p>
      <p class="search_width">
        <Button type="primary" icon="ios-search" @click="search_btn" style="margin-right:10px">查询</Button>
        <Button type="error" icon="ios-trash" @click="clear_btn" style="margin-right:10px">清空</Button>
      </p>
    </div>
    <!-- 列表展示 -->
    <div class="tablebox">
        <Table :columns="columns1" :data="data1" stripe >
          <template slot-scope="{ row, index }" slot="address" v-if="showss==true" >
             <strong>rerewr</strong>
          </template>
        </Table>
        <div style="text-align: right;padding-top:15px">
          <Page :total="100" show-elevator />
        </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value: "",
      showss:false,
      columns1: [
        {
          title: "Name",
          key: "name"
        },
        {
          title: "Age",
          key: "age"
        },
      ],
      data1: [
        {
          name: "John Brown",
          age: 18,
          address: "New York No. 1 Lake Park",
          date: "2016-10-03"
        },
        {
          name: "Jim Green",
          age: 24,
          address: "London No. 1 Lake Park",
          date: "2016-10-01"
        },
        {
          name: "Joe Black",
          age: 30,
          address: "Sydney No. 1 Lake Park",
          date: "2016-10-02"
        },
        {
          name: "Jon Snow",
          age: 26,
          address: "Ottawa No. 2 Lake Park",
          date: "2016-10-04"
        }
      ],
       cityList: [
                    {
                        value: '1',
                        label: '正常出库'
                    },
                    {
                        value: '2',
                        label: '异常出库'
                    },
                ],
                model1: '1'
    };
  },
  mounted() {
    this.setnva();
  },
  methods: {
    setnva() {
      var cc = [
        {
          title: "入库单",
          href: ""
        }
      ];
      this.$store.dispatch("setnvadata", cc);
    },
    search_btn(){

    },
    clear_btn(){

    },
    changestatus(val){
      if(val==2){
        this.showss=true;
        this.columns1=[
          {
            title: "Name",
            key: "name"
          },
          {
            title: "Age",
            key: "age"
          },
          {
            title: "Address",
            slot: "address"
          }
        ]
      }else{
        this.columns1=[
            {
              title: "Name",
              key: "name"
            },
            {
              title: "Age",
              key: "age"
            },
          ];
          this.showss=false;
      }
    }
  }
};
</script>


